.container {
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
    margin-left: auto;
    margin-right: auto;

    @media screen and (min-width: 480px) {
        max-width: 480px;
    }

    @media screen and (min-width: 768px) {
        max-width: 768px;
        padding-left: 16px;
        padding-right: 16px;
    }

    @media screen and (min-width: 1280px) {
        max-width: 1200px;
    }
}